import { HeadFC } from "gatsby";
import React, { useRef } from "react";
import { twMerge } from "tailwind-merge";
import DocSection from "../components/docs/docSection";
import { graph, linear, mix } from "../components/docs/fanCurves";
import Layout from "../components/layout";
import NiceHeader from "../components/niceHeader";
import * as styles from "../styles/docs.module.css";

const sections: DocSection[] = [linear, graph, mix];

const DocSectionComponent = (section: string, onClick: () => void) => {
  return (
    <li
      key={section}
      className="cursor-pointer rounded p-1 hover:bg-slate-200"
      onClick={() => onClick()}
    >
      {section}
    </li>
  );
};

const DocsPage = () => {
  const refs = useRef<Array<HTMLDivElement | null>>([]);

  return (
    <Layout pageTitle="Docs">
      <div className="m-auto flex max-w-5xl p-5">
        {/* Left columm with elements */}
        <div className="flex flex-col border-r-2 border-slate-200 pr-5">
          <h3 className="mb-2 font-medium underline">Fan Curves</h3>
          <ul className="mr-5">
            {sections.map((s, i) =>
              DocSectionComponent(s.key, () => {
                refs.current[i]?.scrollIntoView({ behavior: "smooth" });
              })
            )}
          </ul>
          <h3 className="my-2 font-medium underline">Custom sensors</h3>
          <ul className="mr-5">{DocSectionComponent("Coming soon...", () => {})}</ul>
        </div>

        {/* Main section with actual documentation */}
        <div className={twMerge(styles.doc, "ml-5 space-y-12")}>
          {sections.map((s, i) => {
            return (
              <div key={s.key} ref={(el) => (refs.current[i] = el)}>
                <NiceHeader icon={s.icon} text={s.key}></NiceHeader>
                {s.render()}
              </div>
            );
          })}
        </div>
      </div>
    </Layout>
  );
};

export default DocsPage;

export const Head: HeadFC = () => <title>Docs</title>;
